@use 'base/variables' as v;
@use 'base/mixins' as mix;

.header{
    padding: 5rem 0;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    @include mix.tablet { 
         min-height: 70rem;
    }
    
    &::before{
        content: '';
        background-color: v.$negro;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: .6;
    }

    .contenido-header{
        position: relative;

        h1{
            color: v.$blanco;
            text-align: center;
            text-transform: uppercase;
            margin-top: 5rem;
            font-size: 2.5rem;
            @include mix.tablet { 
                 margin-top: 10rem;
                 font-size: 4rem;
            }
        }
    }
}

